import { createModule } from "vuexok";
import store from "./index";

export default createModule(store, "storeGameModule", {
    namespaced: true,
    state: {
        started: false,
        finished: false,
        paused: false,
        score: 0,
        lives: 0
    },
    mutations: {
        reset(state) {
            state.started = false;
            state.finished = false;
            state.paused = false;
            state.score = 0;
            state.lives = 0;
        },
        setStarted(state) {
            state.started = true;
        },
        setFinished(state) {
            state.finished = true;
        },
        setPause(state, value: boolean) {
            state.paused = value;
        },
        setScore(state, value: number) {
            state.score = value;
        },
        setLives(state, value: number) {
            state.lives = value;
        }

    },
    getters: {
        started(state) {
            const { started } = state;
            return started;
        },
        finished(state) {
            const { finished } = state;
            return finished;
        },
        paused(state) {
            const { paused } = state;
            return paused;
        },
        score(state) {
            const { score } = state;
            return score;
        },
        lives(state) {
            const { lives } = state;
            return lives;
        }
    }
});
