import { createModule } from "vuexok";
import store from "./index";

export default createModule(store, "storeRouteModule", {
    namespaced: true,
    state: {
        currentRoute: ''
    },
    mutations: {
        setCurrentRoute(state, route: string) {
            state.currentRoute = route;
        }
    },
    getters: {
        currentRoute(state) {
            const { currentRoute } = state;
            return currentRoute;
        }
    }
});
